<template>
  <v-container>
    <v-row class="text-center">
      <v-col cols="12">
        <v-img
          src="https://www.pinclipart.com/picdir/big/122-1222184_download-kawaii-animated-gif-clipart-cat-animated-film.png"
          class="my-3"
          contain
          height="200"
        />
      </v-col>
      <v-row class="action" align="center" justify="center" absolute fixed>
        <v-card height="210px" width="500px" color="rgb(0, 0, 0, 0.7)" dark>
          <v-toolbar-title> Jugador </v-toolbar-title>
          <v-card-subtitle>
            <v-form ref="form" v-model="valid">
              <v-text-field
                label="Introduzca su nombre"
                :rules="rules"
                required
                :counter="10"
                v-model="nombre"
              ></v-text-field>
            </v-form>
          </v-card-subtitle>
          <v-dialog
            transition="dialog-bottom-transition"
            max-width="600"
            persistent
          >
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                color="success"
                plain
                v-bind="attrs"
                v-on="on"
                :disabled="!valid"
                >Jugar</v-btn
              >
            </template>
            <template v-slot:default="dialog">
              <v-card color="rgb(0, 0, 0, 0.9)" dark>
                <v-toolbar dark>Numero Magico</v-toolbar>
                <v-card-subtitle>
                  <v-text-field
                    label="Inserte un numero"
                    v-model="numero"
                    :loading="true"
                    v-on:keyup.enter="NumeroMagico"
                    type="number"
                  ></v-text-field>
                  <v-alert color="#2A3B4D" dark icon="mdi-firework" dense
                    >{{ nIntentos }}
                  </v-alert>
                  <v-alert dense>Tiempo: {{ tiempo }} segundos</v-alert>
                </v-card-subtitle>
                <div
                  class="action"
                  align="center"
                  justify="center"
                  absolute
                  fixed
                >
                  <v-btn
                    block
                    color="indigo darken-2"
                    @click="NumeroMagico"
                    dark
                    plain
                    :disabled="!GameOver"
                    >Comprobar</v-btn
                  >
                </div>
                <v-alert
                  :value="alert"
                  transition="fade-transition"
                  icon="mdi-cloud-alert"
                  dense
                  outlined
                  v-bind:type="alertType"
                >
                  {{ textoE }}
                </v-alert>
                <v-card-actions class="justify-end">
                  <v-btn
                    text
                    @click="
                      Cerrar();
                      dialog.value = false;
                    "
                    >Cerrar</v-btn
                  >
                </v-card-actions>
              </v-card>
            </template>
          </v-dialog>
          <!--PUNTUACIONES-->
          <div class="text-center">
            <v-dialog v-model="dialog" width="500">
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  color="red lighten-2"
                  dark
                  plain
                  v-bind="attrs"
                  v-on="on"
                  @click="obtenerDatos"
                >
                  Ganadores
                </v-btn>
              </template>

              <v-card>
                <v-card-title class="text-h5 grey lighten-2">
                  Tier List
                </v-card-title>

                <v-card-text>
                  <v-data-table
                    :headers="headers"
                    :items="tierlist"
                    height="300px"
                  >
                  </v-data-table>
                </v-card-text>

                <v-divider></v-divider>

                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="primary" text @click="dialog = false">
                    Cerrar
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
          </div>
        </v-card>
      </v-row>
    </v-row>
  </v-container>
</template>

<script>
import { collection, addDoc, getDocs } from "firebase/firestore/lite";
import { db } from "../main";
export default {
  name: "HelloWorld",
  data: () => ({
    randomNumber: Math.floor(Math.random() * 101),
    loading: false,
    GameOver: true,
    valid: false,
    tiempo: 0.0,
    dialog: false,
    numero: null,
    tActivo: false,
    interval: null,
    nombre: null,
    alert: false,
    alertType: "warning",
    nIntentos: 10,
    headers: [
      {
        text: "Nombre",
        align: "start",
        sortable: false,
        value: "Nombre",
      },
      { text: "Tiempo", value: "Tiempo" },
    ],
    tierlist: [],
    textoE: "",
    rules: [
      (value) => !!value || "Se requiere un nombre",
      (value) => (value || "").length <= 10 || "Demasiado texto",
    ],
  }),
  methods: {
    TiempoCou() {
      if (this.tActivo) {
        clearInterval(this.interval);
      } else {
        this.interval = setInterval(this.aumentaTiempo, 10);
      }
      this.tActivo = !this.tActivo;
    },
    aumentaTiempo() {
      this.tiempo = parseFloat(this.tiempo) + 0.01;
      this.tiempo = this.tiempo.toFixed(2);
    },
    async agregarDato() {
      await addDoc(collection(db, "Ganadores"), {
        Ganador: this.nombre,
        Tiempo: this.tiempo,
      });
    },
    async obtenerDatos() {
      this.tierlist.length = 0;
      const querySnapshot = await getDocs(collection(db, "Ganadores"));
      querySnapshot.forEach((doc) => {
        let gan = doc.data();
        this.tierlist.push({ Nombre: gan.Ganador, Tiempo: gan.Tiempo });
      });
      this.tierlist.Tiempo.sort();
      console.log(this.tierlist);
    },
    Cerrar() {
      (this.nIntentos = 10),
        (this.textoE = ""),
        (this.randomNumber = Math.floor(Math.random() * 101));
      this.numero = null;
      this.GameOver = true;
      this.nombre = null;
      this.$refs.form.resetValidation();
      if (this.tActivo) {
        this.TiempoCou();
      }
      this.tiempo = 0;
    },
    NumeroMagico() {
      if (!this.tActivo) {
        this.TiempoCou();
      }
      var intentos = this.nIntentos;
      if (intentos == 0) {
        this.alertType = "error";
        this.textoE = "Ha perdido";
        this.GameOver = false;
        this.TiempoCou();
      } else {
        intentos--;
        this.nIntentos = intentos;
        var x = this.numero;
        if (x != this.randomNumber) {
          if (x < this.randomNumber) {
            this.alertType = "warning";
            this.textoE = "Error el numero es mayor";
          } else {
            this.alertType = "warning";
            this.textoE = "Error el numero es menor";
          }
        } else {
          this.alertType = "success";
          this.textoE = "HA GANADO, " + this.nombre;
          this.GameOver = false;
          this.TiempoCou();
          this.agregarDato();
        }
      }
      this.alert = true;
    },
    Ocultar_Alerta: function () {
      window.setInterval(() => {
        this.alert = false;
      }, 4000);
    },
  },
  mounted() {
    if (alert) {
      this.Ocultar_Alerta();
    }
  },
};
</script>
<style>